body {
  color: #222;
  background-color: #fafafa;
  margin: 0;
  padding: 40px 20px;
  font-family: Arial, sans-serif;
}

.title1 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 28px;
}

.sec {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 6px;
  max-width: 180px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 20px;
  display: block;
}

.again {
  color: #fff;
  cursor: pointer;
  background-color: #135e3f;
  border: none;
  border-radius: 6px;
  margin: 30px auto 0;
  padding: 10px 30px;
  font-size: 16px;
  display: block;
}

.again:hover {
  background-color: #d0d0d0;
}

.title2 {
  color: #db7093;
  text-align: center;
  margin-top: 60px;
  margin-bottom: 20px;
  font-size: 28px;
  transition: opacity 1.5s;
}

.circle {
  background-color: #588f5a;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  margin-top: 50px;
  transition: left 2s;
  position: relative;
  left: 0;
}
