body {
  font-family: Arial, sans-serif;
  background-color: #fafafa;
  color: #222;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
  padding: 40px 20px;
  margin: 0;
}

.title1 {
  font-size: 28px;
  margin-bottom: 20px;
  text-align: center;
}

.sec {
  font-size: 20px;
  padding: 10px 20px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: #fff;
  max-width: 180px;
  text-align: center;
  display: block;     /* або можна inline-block */
  margin: 0 auto;
}


.again {
  font-size: 16px;
  padding: 10px 30px;
  border: none;
  border-radius: 6px;
  background-color: #135e3f;
  color: white;
  cursor: pointer;
  display: block;     
  margin: 0 auto;
  margin-top: 30px;
}

.again:hover {
  background-color: #d0d0d0;
}

.title2 {
    color: palevioletred;
  font-size: 28px;
  margin-bottom: 20px;
  margin-top: 60px;
  transition: opacity 1.5s ease;
  text-align: center;
}

.circle {
    border-radius: 50%;
    background-color: rgb(88, 143, 90);
    width: 100px;
    height: 100px;
    margin-top: 50px;
      position: relative;
       left: 0; 
  transition: left 2s ease;
}
